.Hero {
    text-align: center;
    font-size: 40px;
}

.detail-item {
    border: solid 1px rgba(85, 177, 154, 0.2);
    min-height: 100px;
}

.detail-item_text {
    height: 100%;
    display: block;
    width: 100%;
    margin: 15px 0 15px 15px;
}

.item-title,
.item-sub {
    margin: 0;
}

.detail-item_text-bolder {
    font-weight: bolder;
}

.detail-img {
    background-color: whitesmoke;
    width: 150px;
    padding: 10px;
    border-radius: 4px;
}

@media (max-width: 991px) {
    .container .detail-img {
        width: 100%;
        padding: 0;
    }
    .detail-item_text {
        margin: 15px 0;
    }
}
