body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
  color: black;
}

.container {
  margin: 0 auto;
  width: 800px;
}

@media (max-width: 991px) {

  .container,
  .container .detail-img,
  .container .list-img {
    width: 99%;
  }

  .container .detail-item,
  .container .list-item {
    display: block;
  }

  .container .detail-img {
    width: 70%;
    padding: 0;
  }

  .App .hero-header .header>div {
    display: block;
  }

  .search-group .search-input {
    width: 90%;
  }

  .hero-header .header {
    padding: 10px 0;
  }
}
.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

.justify-text {
    text-align: justify;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.hero-header .header {
	color: #fff;
	background-color: #60c6ad;
}

.hero-header .header-brand {
	font-size: 30px;
	margin-right: 30px;
	color: #fff;
}
.search-group {
    display: flex;
}

.search-group .search-input {
    height: 20px;
    border-radius: 4px 0px 0px 4px;
    background: rgba(245, 245, 245, 0.69);
    -webkit-appearance: listitem;
    padding: 6px;
}

.search-group .search-button {
    border-radius: 0px 4px 4px 0px;
    background-color: #009874;
    box-shadow: none;
}

.hero-header .header>div {
    display: flex;
    justify-content: space-between;
}
.Hero {
    text-align: center;
    font-size: 40px;
}

.detail-item {
    border: solid 1px rgba(85, 177, 154, 0.2);
    min-height: 100px;
}

.detail-item_text {
    height: 100%;
    display: block;
    width: 100%;
    margin: 15px 0 15px 15px;
}

.item-title,
.item-sub {
    margin: 0;
}

.detail-item_text-bolder {
    font-weight: bolder;
}

.detail-img {
    background-color: whitesmoke;
    width: 150px;
    padding: 10px;
    border-radius: 4px;
}

@media (max-width: 991px) {
    .container .detail-img {
        width: 100%;
        padding: 0;
    }
    .detail-item_text {
        margin: 15px 0;
    }
}

.hero {
    text-align: center;
    font-size: 40px;
}

.list {
    border: solid 1px rgba(85, 177, 154, 0.2);
}

.list ul {
    padding-top: 0;
}

.list-item {
    min-height: 100px;
}

.list-item_text {
    height: 100%;
    display: block;
    width: 100%;
    margin: 15px 0 15px 15px;
}

.item-title,
.item-sub {
    margin: 0;
}

.list-item_text-bolder {
    font-weight: bolder;
}

.list-img {
    background-color: whitesmoke;
    width: 90px;
    padding: 4px;
    border-radius: 4px;
}

.pagination {
    flex-grow: 1;
}

.pagination > :nth-child(2) {
    display: none;
}

@media (max-width: 991px) {
    .container .detail-img {
        width: 100%;
        padding: 0;
    }
    .list-item_text {
        margin: 15px 0;
    }
}

